import React, { useState} from "react";
import 'bootstrap/dist/css/bootstrap.css';
import styled from "styled-components";
import Title from "../component/Title";
import SubTitle from "../component/SubTitle";
import ItemLabel from "../component/ItemLabel";
import TextForm from "../component/TextForm";
import DateForm from "../component/DateForm";
import SubmitButton from "../component/SubmitButton";
import AddressSelect from "../component/AddressSelect";

const MainWrapper = styled.div`
margin:20px;
  position: relative;
  
  display: flex;
  flex-direction:column;
`

const PersonalInfoPage=()=> {
    const [name, setName] = useState("")
    const [birth,setBirth]=useState("")
    const onNameChange=(event)=>{
        setName(event.target.value)
    }
    const onBirthChange=(event)=>{
        setBirth(event.target.value)
    }

    return (
        <div>
        <MainWrapper>
            <Title content=" "/>
            <SubTitle content="  "/>
            <ItemLabel content=""/>
            <TextForm content="" onChange={onNameChange} value={name}/>
            <ItemLabel content=""/>
            <DateForm value={birth} onChage={onBirthChange}/>
            <ItemLabel content=""/>
            <AddressSelect/>

        </MainWrapper>

            <SubmitButton content=""/>

    </div>
    );
}

export default PersonalInfoPage;